import { faker } from '@faker-js/faker';
import { Newsletter as INewsletter } from '@/models/newsletter.model';
import { CATEGORIES } from '@/constants/categories';

export const getRandomText = (min: number, max: number): string => {
	const length = Math.floor(Math.random() * (max - min + 1)) + min;
	return faker.lorem.sentence(length);
};

/**
 * Picsumì„ ì´ìš©í•œ ëžœë¤ ì´ë¯¸ì§€ URL ë°˜í™˜
 * @param width ì´ë¯¸ì§€ ë„ˆë¹„ (ê¸°ë³¸ê°’: 640)
 * @param height ì´ë¯¸ì§€ ë†’ì´ (ê¸°ë³¸ê°’: 480)
 * @returns ëžœë¤ ì´ë¯¸ì§€ URL
 */
export const getRandomImage = (width: number = 640, height: number = 480): string => {
	return `https://picsum.photos/${width}/${height}?random=${Math.floor(Math.random() * 1000)}`;
};

/**
 * Picsumì„ ì´ìš©í•œ ëžœë¤ ì´ë¯¸ì§€ ì—¬ëŸ¬ ê°œ ë°˜í™˜
 * @param count ë°˜í™˜í•  ì´ë¯¸ì§€ ê°œìˆ˜
 * @param width ì´ë¯¸ì§€ ë„ˆë¹„
 * @param height ì´ë¯¸ì§€ ë†’ì´
 * @returns ëžœë¤ ì´ë¯¸ì§€ URL ë°°ì—´
 */
export const getRandomImages = (count: number, width: number = 640, height: number = 480): string[] => {
	return Array.from({ length: count }, () => getRandomImage(width, height));
};

export const getNewsletters = (): INewsletter[] => {
	const categories = CATEGORIES.filter((category) => category.title !== 'ì „ì²´').map((category) => category.title);

	return Array.from({ length: 5 }, (_, dayIndex) => {
		const baseDate = new Date(new Date().toLocaleString('en-US', { timeZone: 'Asia/Seoul' }));
		baseDate.setDate(baseDate.getDate() - dayIndex);
		const dateString = baseDate.toLocaleDateString('en-CA'); // YYYY-MM-DD í˜•ì‹

		return categories.map((category, categoryIndex) => ({
			id: dayIndex * categories.length + categoryIndex + 1,
			category,
			date: dateString,
			title: getRandomText(10, 25),
			summary: getRandomText(20, 80),
			image: getRandomImage(),
			link: `/news/${dayIndex * categories.length + categoryIndex + 1}`,
			views: Math.floor(Math.random() * 1000),
			likes: Math.floor(Math.random() * 500),
			comments: Math.floor(Math.random() * 300),
		}));
	}).flat();
};
